.panchakarma{
    max-width: 1200px;
    padding: 30px;
    font-family: 'Poppins', sans-serif;
}
p{
    font-size: 1.3rem;
}
h1, h2, h3, h5 {
    line-height: 4rem;
    font-weight: bold;
}
li{
    font-size: 1.3rem;
}
ul{
    list-style:circle;
    margin-left: 20px;
}
.book__button{
    font-size: 1.3rem;
    float: right;
    margin-top: 20px;
}
@media screen and (max-width:768px){
    .book__button{
        float: left;
        font-size: 1rem;
    }
}